* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'TT Norms', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  background: url("../img/bckg.jpg") center center no-repeat;
  background-attachment: fixed;
  background-size: cover;

  &::before{
    content: '';
    position: fixed;
    width: 100%;
    height: 100%;
    display: block;
    left: 0;
    top: 0;
    background-image: linear-gradient(to right, rgba(11, 4, 125, 0.9) 0%, rgba(101, 4, 125, 0.2) 100%);
  }
}

.header,.footer{
  position: relative;
  z-index: 2;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.content {
  position: relative;
  z-index: 2;
  flex: 1 1 auto;

  &__wrapper {

    padding: 30px 25px;
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1);

    & + &{
      margin-top: 40px;
    }
    &_small{
      padding: 20px 15px;
    }
  }

  &__container {
    max-width: 400px;
    padding: 0 15px;
    width: 100%;
    margin: 0 auto;

  }
}

.form {

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__content{
    margin-top: 15px;
  }

  &__title {
    font-size: 21px;
    color: rgb(51, 51, 51);
    line-height: 1.1;
    letter-spacing: 0.04em;
    text-align: left;
  }
  &__btn {
    margin: 30px 0 25px 0;

    &_enter {
      margin-bottom: 0;
    }
  }

  &__agree {
    font-family: "Open Sans",sans-serif;
    font-size: 14px;
    text-align: center;
    color: rgb(51, 51, 51);

    &.checkbox {
      text-align: left;
    }

    a{
      text-decoration: underline;
      text-decoration-style: dashed;
    }
  }

  &-item {
    & + &{
      margin-top: 25px;
    }
    &__label {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;

    }

    &__placeholder {
      font-family: 'Open Sans', sans-serif;
      font-size: 13px;
      color: rgba(51, 51, 51,.8);
      display: block;
      margin-bottom: 6px;
    }

    &__input {
      box-shadow: none;
      position: relative;

      color:rgba(0, 0, 0, 0.5);
      border: 2px solid rgb(120, 120, 120);
      border-radius: 6px;

      height: 40px;
      width: 100%;

      padding:10px 8px;

      font-size: 14px;
      cursor: initial;

      background-color: transparent;
      transition: .3s border-color linear;

      outline: none;

      &:focus {
        border-color: rgb(93, 27, 235);
      }
    }
  }
}

.tabs {
  &-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 35px;

    &__item {
      margin: 0 15px;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        height: 3px;
        width: 0;
        display: block;
        background-color: rgb(179, 124, 71);
        bottom: -8px;
        left: 0;
        transition: .3s width;
      }

      &.active {
        &::before {
          width: 100%;
        }
      }
    }

    &__link {
      font-size: 20px;
      color: #fff;
      font-weight: 500;
      letter-spacing: 0;
      line-height: 1.4;
      text-transform: uppercase;
    }
  }

  &-content {
    &__item {
      display: none;

      &.active {
        display: block;
      }
    }
  }
}

.btn {
  -webkit-appearance: none;

  display: flex;
  align-items: center;
  justify-content: center;

  border: none;
  outline: none;
  height: 45px;

  font-size: 16px;
  color: #fff;
  text-transform: uppercase;

  border-radius: 5px;

  transition: .3s background-color linear, .3s color linear;

  &:hover {
    cursor: pointer;
  }

  &-green {
    width: 100%;
    background-color: rgb(201, 157, 116);

    &:hover {
      background-color: rgb(196, 145, 96);
    }
  }

  &-yellow {
    background-color: rgb(255, 107, 0);
    max-width: 210px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    &:hover {
      background-color: rgb(207, 204, 0);
    }
  }
  &-orange{
    background-color: rgb(255, 235, 60);
    width: 100%;
    color:#000;
    &:hover{
      background-color: rgb(232, 0, 223);
      color:#fff;
    }
  }
}

.checkbox {

  &__input {
    position: absolute;

    &:checked ~ .checkbox__checkmark {
      background-color: rgb(92, 22, 241);
    }
  }

  &__label {
    display: flex;
    align-items: flex-start;
    &:hover {
      cursor: pointer;
    }
  }

  &__text {
    font-family: "Open Sans",sans-serif;
    font-size: 14px;
    color: rgb(51, 51, 51);
  }

  &__checkmark {
    width: 22px;
    height: 22px;
    margin: 0 4.5px 0 0;
    display: inline-block;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.25);
    position: relative;
    border-radius: 2px;
    flex-shrink: 0;

    transition: .3s background-color linear;
  }
}

.forgot-password {
  font-size: 14px;
  color: rgb(201, 201, 201);
  transition: .3s color;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  margin: 10px 0;

  &:hover {
    color: #000;
  }
}

.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__banner{
    img{
      max-height: 78px;
    }
  }

  &__text {
    padding: 0 40px;
    font-size: 20px;
    color: #fff;
    line-height: 1.2;
    letter-spacing: .04em;
    text-align: center;

    p + p {
      margin-top: 20px;
    }
  }

  &__checkbox {
    margin-top: 40px;
    padding: 0 38px;

    .checkbox__text {
      font-size: 10px;
      vertical-align: super;
    }
  }

  &__coffee {
    margin-top: 40px;
    width: 100%;
  }
}

p {
  .bold {
    font-weight: 700;
  }

  .crossed {
    text-decoration: line-through;
  }

  .text-small {
    font-size: 15px;
  }
}

.payment-block {
  padding: 20px 30px 30px 30px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;

  &__title {
    font-size: 22px;
    line-height: 1.2;
    letter-spacing: .04em;
    color: #fff;
    text-align: center;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    font-size: 270%;
    img{
      max-height: 150px;
    }
  }

  &__btn {
    margin-top: 20px;
  }
}

.payment{
  &__title{
    text-align: center;
    font-size: 22px;
    line-height: 1.2;

    &_bordered{
      font-size: 26px;
      font-weight: 500;
      letter-spacing: .04em;
      width: max-content;
      border-bottom: 3px solid rgb(201, 157, 116);
      padding-bottom: 5px;
      margin: 0 auto 10px auto;
    }
  }
  &__btn{
    margin-top: 20px;
    font-size: 20px;
    font-weight: 700;
  }
  &__agree{
    margin-top: 15px;
    .checkbox__checkmark{
      width: 14px;
      height: 14px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .checkbox__text{
      font-size: 10px;
    }
  }
}

.promotion{
  margin-top: 10px;
  &__text{
    text-align: center;
    font-size: 18px;
    line-height: 1.2;
  }
  &__text + &__text{
    margin-top: 5px;
  }
  &__title{
    font-size: 24px;
    margin-bottom: 6px;
    span{
      text-decoration: line-through;
    }
  }
}

@media screen and (max-width: 475px) {
  .content{
    &__wrapper{
      padding: 20px 15px;
    }
  }
}