.header {
  flex-shrink: 0;
  &__wrapper{
    padding: 50px 0;
  }
  &__logo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.logo {
  display: flex;
  flex-direction: column;
  &__img {
    max-height: 50px;
    width: 100%;
    object-fit: contain;
  }
  &__text {
    font-size: 19px;
    font-family: 'Bebas Neue', sans-serif;
    line-height: 1.6;
    letter-spacing: 0;
    opacity: .65;
    color: #fff;
    text-transform: uppercase;

    text-align: center;
  }
}