@media screen and (max-width: 600px){
  .footer{
    &-list{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      row-gap: 10px;
      &__item:nth-child(3){
        grid-column-start: 1;
        grid-column-end: 3;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .payment-block__img{
    font-size: 330%;
  }
  .payment__title{
    font-size: 24px;
    &_bordered{
      font-size: 24px;
    }
  }
  .promotion__text{
    font-size: 16px;
  }
  .promotion__title{
    margin-bottom: 0;
    font-size: 24px;
  }
  .promotion__text + .promotion__text{
    margin-top: 0;
  }
  .payment-block{
    padding: 20px 0;
  }
  .welcome__text{
    padding: 0;
    p{
      margin: 0 auto;
      &:nth-child(1){
        max-width: 210px;
      }
      &:nth-child(2){
        max-width: 215px;
      }
      &:nth-child(3){
        max-width: 182px;
      }
    }
  }
  .footer{
    padding-top: 20px;
    margin-top: 70px;
  }
  .footer-list{
    row-gap: 20px;
    column-gap: 20px;
    &__item{
      &:nth-child(1){
        justify-content: flex-end;
      }
      &:nth-child(2){
        justify-content: flex-start;
      }
      &:nth-child(3){
        grid-column-start: 1;
        grid-column-end: 3;
        justify-content: center;
      }
    }
    &__link{
      font-size: 10px;
    }
  }
  .form{
    &__content{
    }
  }
  .btn{
    height: 45px;
    font-size: 16px;
    &-yellow{
      max-width: 100%;
    }
    &-orange{
      max-width: 190px;
      margin-right: auto;
      margin-left: auto;
    }
  }
  .welcome{
    &__banner{
      img{
        max-height: 75px;

      }
    }
    &__coffee{
      margin-top: 30px;
    }
    &__checkbox{
      padding: 0;
      margin-top: 25px;
    }
  }
  .payment-block{
    &__title{
      font-size: 20px;
    }

    &__img{
      img{
        object-fit: contain;
      }
    }
  }
}