.footer {
  flex-shrink: 0;
  height: 100px;
  padding-top: 40px;
  &-list {
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    &__item{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__link {
      font-size: 16px;
      color: #fff;
      text-transform: uppercase;
      transition: .3s color linear;

      &:hover{
        color:rgb(179, 124, 71) ;
      }
    }
  }
}