*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'TT Norms';
  src: url("../fonts/TTNorms-Regular/TTNorms-Regular.woff2") format("woff2"), url("../fonts/TTNorms-Regular/TTNorms-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'TT Norms';
  src: url("../fonts/TTNorms-Medium/TTNorms-Medium.woff2") format("woff2"), url("../fonts/TTNorms-Medium/TTNorms-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'TT Norms';
  src: url("../fonts/TTNorms-Bold/TTNorms-Bold.woff2") format("woff2"), url("../fonts/TTNorms-Bold/TTNorms-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Bebas Neue';
  src: url("../fonts/BebasNeueBook/BebasNeueBook.woff2") format("woff2"), url("../fonts/BebasNeueBook/BebasNeueBook.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Regular/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular/OpenSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'TT Norms', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: url("../img/bckg.jpg") center center no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

body::before {
  content: '';
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  left: 0;
  top: 0;
  background-image: linear-gradient(to right, rgba(11, 4, 125, 0.9) 0%, rgba(101, 4, 125, 0.2) 100%);
}

.header, .footer {
  position: relative;
  z-index: 2;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.content {
  position: relative;
  z-index: 2;
  flex: 1 1 auto;
}

.content__wrapper {
  padding: 30px 25px;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1);
}

.content__wrapper + .content__wrapper {
  margin-top: 40px;
}

.content__wrapper_small {
  padding: 20px 15px;
}

.content__container {
  max-width: 400px;
  padding: 0 15px;
  width: 100%;
  margin: 0 auto;
}

.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form__content {
  margin-top: 15px;
}

.form__title {
  font-size: 21px;
  color: #333333;
  line-height: 1.1;
  letter-spacing: 0.04em;
  text-align: left;
}

.form__btn {
  margin: 30px 0 25px 0;
}

.form__btn_enter {
  margin-bottom: 0;
}

.form__agree {
  font-family: "Open Sans",sans-serif;
  font-size: 14px;
  text-align: center;
  color: #333333;
}

.form__agree.checkbox {
  text-align: left;
}

.form__agree a {
  text-decoration: underline;
  text-decoration-style: dashed;
}

.form-item + .form-item {
  margin-top: 25px;
}

.form-item__label {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}

.form-item__placeholder {
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  color: rgba(51, 51, 51, 0.8);
  display: block;
  margin-bottom: 6px;
}

.form-item__input {
  box-shadow: none;
  position: relative;
  color: rgba(0, 0, 0, 0.5);
  border: 2px solid #787878;
  border-radius: 6px;
  height: 40px;
  width: 100%;
  padding: 10px 8px;
  font-size: 14px;
  cursor: initial;
  background-color: transparent;
  transition: .3s border-color linear;
  outline: none;
}

.form-item__input:focus {
  border-color: #5d1beb;
}

.tabs-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}

.tabs-nav__item {
  margin: 0 15px;
  position: relative;
}

.tabs-nav__item::before {
  content: '';
  position: absolute;
  height: 3px;
  width: 0;
  display: block;
  background-color: #b37c47;
  bottom: -8px;
  left: 0;
  transition: .3s width;
}

.tabs-nav__item.active::before {
  width: 100%;
}

.tabs-nav__link {
  font-size: 20px;
  color: #fff;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 1.4;
  text-transform: uppercase;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.btn {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  height: 45px;
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  transition: .3s background-color linear, .3s color linear;
}

.btn:hover {
  cursor: pointer;
}

.btn-green {
  width: 100%;
  background-color: #c99d74;
}

.btn-green:hover {
  background-color: #c49160;
}

.btn-yellow {
  background-color: #ff6b00;
  max-width: 210px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.btn-yellow:hover {
  background-color: #cfcc00;
}

.btn-orange {
  background-color: #ffeb3c;
  width: 100%;
  color: #000;
}

.btn-orange:hover {
  background-color: #e800df;
  color: #fff;
}

.checkbox__input {
  position: absolute;
}

.checkbox__input:checked ~ .checkbox__checkmark {
  background-color: #5c16f1;
}

.checkbox__label {
  display: flex;
  align-items: flex-start;
}

.checkbox__label:hover {
  cursor: pointer;
}

.checkbox__text {
  font-family: "Open Sans",sans-serif;
  font-size: 14px;
  color: #333333;
}

.checkbox__checkmark {
  width: 22px;
  height: 22px;
  margin: 0 4.5px 0 0;
  display: inline-block;
  background: #fff;
  border: 2px solid rgba(0, 0, 0, 0.25);
  position: relative;
  border-radius: 2px;
  flex-shrink: 0;
  transition: .3s background-color linear;
}

.forgot-password {
  font-size: 14px;
  color: #c9c9c9;
  transition: .3s color;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  margin: 10px 0;
}

.forgot-password:hover {
  color: #000;
}

.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcome__banner img {
  max-height: 78px;
}

.welcome__text {
  padding: 0 40px;
  font-size: 20px;
  color: #fff;
  line-height: 1.2;
  letter-spacing: .04em;
  text-align: center;
}

.welcome__text p + p {
  margin-top: 20px;
}

.welcome__checkbox {
  margin-top: 40px;
  padding: 0 38px;
}

.welcome__checkbox .checkbox__text {
  font-size: 10px;
  vertical-align: super;
}

.welcome__coffee {
  margin-top: 40px;
  width: 100%;
}

p .bold {
  font-weight: 700;
}

p .crossed {
  text-decoration: line-through;
}

p .text-small {
  font-size: 15px;
}

.payment-block {
  padding: 20px 30px 30px 30px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
}

.payment-block__title {
  font-size: 22px;
  line-height: 1.2;
  letter-spacing: .04em;
  color: #fff;
  text-align: center;
}

.payment-block__img {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  font-size: 270%;
}

.payment-block__img img {
  max-height: 150px;
}

.payment-block__btn {
  margin-top: 20px;
}

.payment__title {
  text-align: center;
  font-size: 22px;
  line-height: 1.2;
}

.payment__title_bordered {
  font-size: 26px;
  font-weight: 500;
  letter-spacing: .04em;
  width: max-content;
  border-bottom: 3px solid #c99d74;
  padding-bottom: 5px;
  margin: 0 auto 10px auto;
}

.payment__btn {
  margin-top: 20px;
  font-size: 20px;
  font-weight: 700;
}

.payment__agree {
  margin-top: 15px;
}

.payment__agree .checkbox__checkmark {
  width: 14px;
  height: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment__agree .checkbox__text {
  font-size: 10px;
}

.promotion {
  margin-top: 10px;
}

.promotion__text {
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
}

.promotion__text + .promotion__text {
  margin-top: 5px;
}

.promotion__title {
  font-size: 24px;
  margin-bottom: 6px;
}

.promotion__title span {
  text-decoration: line-through;
}

@media screen and (max-width: 475px) {
  .content__wrapper {
    padding: 20px 15px;
  }
}

.header {
  flex-shrink: 0;
}

.header__wrapper {
  padding: 50px 0;
}

.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  display: flex;
  flex-direction: column;
}

.logo__img {
  max-height: 50px;
  width: 100%;
  object-fit: contain;
}

.logo__text {
  font-size: 19px;
  font-family: 'Bebas Neue', sans-serif;
  line-height: 1.6;
  letter-spacing: 0;
  opacity: .65;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
}

.footer {
  flex-shrink: 0;
  height: 100px;
  padding-top: 40px;
}

.footer-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.footer-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-list__link {
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;
  transition: .3s color linear;
}

.footer-list__link:hover {
  color: #b37c47;
}

@media screen and (max-width: 600px) {
  .footer-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    row-gap: 10px;
  }
  .footer-list__item:nth-child(3) {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media screen and (max-width: 450px) {
  .payment-block__img {
    font-size: 330%;
  }
  .payment__title {
    font-size: 24px;
  }
  .payment__title_bordered {
    font-size: 24px;
  }
  .promotion__text {
    font-size: 16px;
  }
  .promotion__title {
    margin-bottom: 0;
    font-size: 24px;
  }
  .promotion__text + .promotion__text {
    margin-top: 0;
  }
  .payment-block {
    padding: 20px 0;
  }
  .welcome__text {
    padding: 0;
  }
  .welcome__text p {
    margin: 0 auto;
  }
  .welcome__text p:nth-child(1) {
    max-width: 210px;
  }
  .welcome__text p:nth-child(2) {
    max-width: 215px;
  }
  .welcome__text p:nth-child(3) {
    max-width: 182px;
  }
  .footer {
    padding-top: 20px;
    margin-top: 70px;
  }
  .footer-list {
    row-gap: 20px;
    column-gap: 20px;
  }
  .footer-list__item:nth-child(1) {
    justify-content: flex-end;
  }
  .footer-list__item:nth-child(2) {
    justify-content: flex-start;
  }
  .footer-list__item:nth-child(3) {
    grid-column-start: 1;
    grid-column-end: 3;
    justify-content: center;
  }
  .footer-list__link {
    font-size: 10px;
  }
  .btn {
    height: 45px;
    font-size: 16px;
  }
  .btn-yellow {
    max-width: 100%;
  }
  .btn-orange {
    max-width: 190px;
    margin-right: auto;
    margin-left: auto;
  }
  .welcome__banner img {
    max-height: 75px;
  }
  .welcome__coffee {
    margin-top: 30px;
  }
  .welcome__checkbox {
    padding: 0;
    margin-top: 25px;
  }
  .payment-block__title {
    font-size: 20px;
  }
  .payment-block__img img {
    object-fit: contain;
  }
}
